        height: 110vh;
        display: grid;
        grid-template-columns: 0 1fr;
    }*/

  .registerMainComponent {
    height: 100%;
  }
  .registerSVG img {
    display: none;
  }
  .registerSection {
    grid-template-columns: 1fr;
  }
  .loginContainer .title {
    margin: 1rem;
  }
  .loginContainer {
    height: 100%;
    margin-top: 1rem;
    grid-template-columns: 1fr;
  }

  form {
    padding: 7%;
  }

  .headerContainer {
    height: 4rem;
  }
  .container {
    height: 100vh;
  }
  .title {
    font-size: 2rem;
  }
  .codingSection {
    padding-top: 1rem;
    display: flex;
    flex-direction: column-reverse;
  }

  .codingInfo {
    display: flex;
    align-items: center;
  }
  #uni {
    text-align: center;
    flex-direction: column-reverse;
  }
  .btn {
    padding: 15px 30px;
  }
  .Headerbtn {
    padding: 10px;
  }
  .codingimage img {
    width: 20rem;
  }
  .infoCoding {
    font-size: 1.7rem;
  }

  .feedSection {
    display: flex;
    text-align: center;
    flex-direction: column;
    margin-top: 1.5rem;
  }
  .info {
    font-size: 1.5rem;
    -webkit-text-stroke: 1px #1c1e29;
    font-weight: bold;
  }
  .wave {
    height: 15rem;
  }
  .wave p {
    font-size: 1rem;
  }
}

@media (min-width: 768px) and (max-width: 991px) {
  /* small Devices */

  .title {
    font-size: 3rem;
  }
  .infoCoding {
    font-size: 1.5rem;
  }
  .info {
    font-size: 1.7rem;
  }
  .wave {
    height: 13rem;
  }
  form {
    padding: 5%;
  }

  .registerSVG {
    display: flex;
    justify-content: center;
  }
  .registerSVG img {
    height: 20rem;
    width: 20rem;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  /* Large Tablets and laptop Devices */

  .container {
    height: 120vh;
  }
  .infoCoding {
    font-size: 1.7rem;
  }
  .info {
    font-size: 2rem;
  }
  .wave {
    height: 10rem;
  }
  form {
    padding: 5%;
  }
  .registerSVG {
    display: flex;
    justify-content: center;
  }
  .registerSVG img {
    height: 20rem;
    width: 20rem;
  }
}

@media (min-width: 1200px) {
  /* Desktop Devices */

  .codingimage img {
    margin-top: 2rem;
    height: 25rem;
    width: 30rem;
    margin-bottom: 1rem;
  }
  .infoCoding {
    font-size: 2rem;
  }
  .wave {
    height: 15rem;
  }

  /* Registration Form Design */
  .registerMainComponent {
    height: 120vh;
  }
}
